@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&family=Edu+VIC+WA+NT+Beginner:wght@600&family=Open+Sans&family=Poppins:wght@200;300;500;600;700;800&display=swap');


*{
    margin: 0;
    padding: 0;
    text-decoration: none;
    font-family: 'Poppins', sans-serif;
}
h1{
    color: #242F9B;
    font-size: 30px;
    margin-bottom: 15px;
}
span{
    color:#DBDFFD;
}
.container{
    display: flex;
    flex-direction: row;
}
main{
    width: 100%;
    padding: 20px;
}
.icone{
    height: 30px;
    margin-right: 20px;
    transition: all 0.3s;
    color: #143F6B;
}
.link:hover .icone{
    height: 35px;
    color: #6FEDD6;
}

.item-text{
    color: #000;
    font-weight: 200;
    font-size: 18px;
}
.link{
    padding: 22px;
    line-height: 30px;
    margin-left: 15px;
   display: flex;
}
.Sidebar{
    height: 682px;
    padding: 30px;
    border-right: 1px solid #000;
}
.active{
    background: #EFEFEF ;
    border-radius: 12px;
}

